                                text-align: left;
                                padding-left: 5px;

                                &:hover {
                                    transform: translateX(-5px);
                                }

                                span {
                                    &:nth-of-type(1) {
                                        margin-right: 3px;
                                        background: url("./images/slid_04.png") no-repeat center center;
                                        background-size: contain;
                                    }

                                }

                                span {
                                    &:nth-of-type(1) {
                                        display: inline-block;
                                        font-style: normal;
                                        width: 15px;
                                        height: 15px;
                                        font-size: 12px;
                                        color: #fff;
                                        line-height: 15px;
                                        text-align: center;
                                    }

                                    &:nth-of-type(2) {
                                        font-size: 14px;
                                    }

                                    &:nth-of-type(3) {
                                        display: inline-block;
                                        vertical-align: text-top;
                                        width: 13px;
                                        height: 15px;
                                    }
                                }

                                &:nth-of-type(1) {
                                    span {
                                        &:nth-of-type(1) {

                                            background: url("./images/slid_01.png") no-repeat center center;
                                            background-size: contain;

                                        }


                                        &:nth-of-type(3) {

